@font-face
{
    font-family: "Brandon Grotesque";
    color: white;
    src: local("Brandon Grotesque"), url("./fonts/brandon-grotesque-black.otf");
}

body {
    background-color: black;
    color: white;
    font-family: "Brandon Grotesque";
}

.hover-underline-animation {
    display: inline-block;
    position: relative;
    color: white;
}

.hover-underline-animation:hover:after {
  transform: scale(1);
  transform-origin: bottom left;
}

.hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scale(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: white;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.hover-underline-animation:hover:after {
    transform:scale(1);
    transform-origin: bottom left;
}

.ul_r {
    float: right;
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100px;
    background-color: black;
    text-align: right;
}

.nostyle {
  display: block;
  color: white;
  padding: 0px 16px;
  text-decoration: none;
}
